import React from "react";
import styled from "styled-components";
// Components
import ProjectBox from "../Elements/ProjectBox";
import FullButton from "../Buttons/FullButton";
// Assets
import ProjectImg1 from "../../assets/img/projects/office.png";
import ProjectImg2 from "../../assets/img/projects/dormitory.png";
import ProjectImg3 from "../../assets/img/projects/canteen.png";
import ProjectImg4 from "../../assets/img/projects/train.png";
import ProjectImg5 from "../../assets/img/projects/teach.png";
import ProjectImg6 from "../../assets/img/projects/security.png";
import AddImage2 from "../../assets/img/projects/cetificate.png";
import Cetificate1 from "../../assets/img/projects/goodcompany.png"
import Cetificate2 from "../../assets/img/projects/maincompany.png"
import Cetificate3 from "../../assets/img/projects/goodcompany1.png"

export default function Projects() {
  return (
    <Wrapper id="projects">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">公司情况简介</h1>
            <p className="font13">
              宇轩公司/广恒达公司位于天津市宁河县潘庄工业园，从2013年五月份开始进行铸造试运行，占地180 亩，紧邻205国道，东临津唐高速公路，距离天津市区20公里，距离港口65公里，距空港15公里，距铁路40公里，地理位置利于生产用的原材料运入，产品的运出
              <br />
              现有设备200多台，员工400多人，技术人员30多人
            </p>
          </HeaderInfo>
          <div className="row textCenter">
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg1}
                title="办公楼"
                text="Our Office building"
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg2}
                title="宿舍楼"
                text="Our Dormitory."
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg3}
                title="员工食堂"
                text="Our Canteen."
              />
            </div>
          </div>
          <div className="row textCenter">
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg4}
                title="加工道场"
                text="Machining training center"
                action={() => alert("clicked")}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg5}
                title="教研室"
                text="Teaching and research room"
                action={() => alert("clicked")}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg6}
                title="安全入口及看板"
                text="Security entrance and billboard"
                action={() => alert("clicked")}
              />
            </div>
          </div>
          <div className="row flexCenter">
            <div style={{ margin: "50px 0", width: "200px" }}>
              {/*<FullButton title="Load More" action={() => alert("clicked")} />*/}
            </div>
          </div>
        </div>
      </div>
      <div className="lightBg">
        <div className="container">
          <Advertising className="flexSpaceCenter">
            <AddLeft>
              <AddLeftInner>
                <ImgWrapper className="flexCenter">
                  <img className="radius8" src={AddImage2} alt="add" />
                </ImgWrapper>
              </AddLeftInner>
            </AddLeft>
            <AddRight>
              <h2 className="font40 extraBold">ISO三体系认证</h2>
              <p className="font12">
                我们取得了ISO三体系认证，并多次获得过行业荣誉
              </p>
              <ButtonsRow className="flexNullCenter" style={{ margin: "30px 0" }}>
                <div style={{ width: "300px" }}>
                  <ImgWrapper className="flexCenter">
                    <img className="radius8" src={Cetificate1} alt="add" />
                  </ImgWrapper>
                </div>
                <div style={{ width: "300px", marginLeft: "15px" }}>
                  <ImgWrapper className="flexCenter">
                    <img className="radius8" src={Cetificate2} alt="add" />
                  </ImgWrapper>
                </div>
                <div style={{ width: "300px", marginLeft: "15px" }}>
                  <ImgWrapper className="flexCenter">
                    <img className="radius8" src={Cetificate3} alt="add" />
                  </ImgWrapper>
                </div>
              </ButtonsRow>
            </AddRight>
          </Advertising>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;
